import React from 'react'
import '../styles/_TopDownvideo.scss';
import Button from '@mui/material/Button';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { Link } from 'react-router-dom';


function TopDownVideo(props) {

  return (
    <>
      <div className='text-white child-div'>
        <h1 className='text-white mt-5'>Digital Marketing<br/> That Drives Result</h1>
        <p>Propel your business to new heights with our award-winning <br/> digital marketing services and technology platform.</p>
        <Button className='m-2' variant="contained">+92 306 444 7013</Button>
       <Button variant="contained" color="success">
  <Link to="/inquiry-form" className="text-white text-decoration-none">Get a Proposal<ArrowForwardIosIcon/></Link>
</Button>

        </div>
        <img src="../sources/images/digital.jpg" style={{objectFit:"cover"}} width="100%" height="600px" alt="img" />
      
    </>
  )
}

export default TopDownVideo