import React, { useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import Button from '@mui/material/Button';
import HeaderName from './HeaderName'
import Footer from './Footer';
const img5="../sources/images/landing_img_8.png"

function InquiryForm() {
    const location=useLocation();
    useEffect(() => {
      document.title = "InQuiry Form | TRVL";
    }, [])
    
  return (
    <div>
        <HeaderName name="Inquiry Form" location={location.pathname} />

     <div className="container p-3">
       <div className="row">
         <div className="col-md-6">
        <img className='img-fluid' src={img5}  alt="img"/>
         </div>
         <div className="col-md-6">
           <h4>Request for a Quotation</h4>
           <span className='bg-black text-white'><b>Give us a bit of detail on your web project and let us help!</b></span>
           <form className="row g-3">
  <div className="col-md-6">
    <input type="text" placeholder='Your Name*' className="form-control border-0 border-bottom border-danger" id="inputtext4"/>
  </div>
  <div className="col-md-6">
    <input type="email" placeholder='Email*' className="form-control border-0 border-bottom border-danger" id="inputemail4"/>
  </div>
  <div className="col-md-6">
    <input type="number" placeholder="Phone*" className="form-control border-0 border-bottom border-danger" id="inputAddress"/>
  </div>
  <div className="col-md-6">
    <input type="text" className="form-control border-0 border-bottom border-danger" id="inputwebsite2" placeholder="Company*"/>
  </div>
  <div className="col-md-12">
    <input type="text" className="form-control border-0 border-bottom border-danger" id="inputwebsite2" placeholder="Website*"/>
  </div>
  <span>Choose Your Service</span>
 <div className="col-md-4">
 <div class="form-check">
  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
  <label class="form-check-label" for="flexCheckDefault">
    Web Development
  </label>
</div>
 </div>
 <div className="col-md-4">
 <div class="form-check">
  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
  <label class="form-check-label" for="flexCheckDefault">
    SEO
  </label>
</div>
 </div>
 <div className="col-md-4">
 <div class="form-check">
  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
  <label class="form-check-label" for="flexCheckDefault">
    Paid Marketing
  </label>
</div>
 </div>
 <div className="col-md-12">
 <div class="form-check">
  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
  <label class="form-check-label" for="flexCheckDefault">
  Social Media Management
  </label>
</div>
 </div>
  <div className="col-md-12">
    <textarea class="form-control border-0 border-bottom border-danger" placeholder='Message' id="exampleFormControlTextarea3" rows="7"></textarea>
  </div>
  <div className="col-12">
  <Button type="submit" variant="contained" color="success">
  Get Quote Now
</Button>
  </div>
</form>
           </div>
       </div>
     </div>
     <Footer/>
    </div>
  )
}

export default InquiryForm