import React from 'react'
import Topgriddata from './griddata'
import {BottomGriddata} from './griddata';
function GridGallery() {

function ncard(e){
return(
  <div className="col-md" key={e.id}>
  <div className="m-3">
        <img src={e.img} style={{width: "500px",height:"400px"}} className="card-img-top rounded img-fluid" alt="..."/>
         <div className="card-body border shadow" style={{width:"100%"}}>
         <p className="card-text text-center">{e.description}</p>
   </div>
  </div>
   </div>
)
}
function ncardtwo(t){
  return (
    <div className="mt-4 text-center col-md" key={t.id}>
    <div className="m-3" >
          <img src={t.image} style={{width: "300px",height:"300px"}} className="card-img-top rounded img-fluid" alt="..."/>
           <div className="card-body border rounded shadow" style={{width:"100%"}}>
           <p className="card-text text-center">{t.des}</p>
     </div>
    </div>
     </div>
  )
}





  return (
    <div className='container mt-5 gridgallery'>
    <h1 className='text-center'>Latest Works</h1>
    <div className="row mt-4">
   {Topgriddata.map(ncard)}
    </div>
<div className="row">
  {BottomGriddata.map(ncardtwo)}
</div>



    </div>
  )
}

export default GridGallery