import React, { useEffect } from 'react'
import { useLocation } from 'react-router-dom';
import {servicepagedata} from '../components/griddata';
import '../styles/_ServicesPage.scss';
import Accourdion from './Accourdion';
import Footer from './Footer';
import HeaderName from './HeaderName';
const servicesimg='../sources/images/servicespage.png';
function ServicesPage() {
const location=useLocation();
useEffect(() => {
  document.title = "Web Development & eCommerce Services";
}, [])
function sdata(e){
    return(
      <div key={e.id} className="col-md">
          <img className='img-fluid mt-2' style={{height:"30%"}} src={e.simg} alt="img"/>
          <h3>{e.sheading}</h3>
          <p>{e.spara}</p>
      </div>
    )
}

  return (
    <>
<HeaderName name="Services" location={location.pathname} />

<div className='container-fluid'>
<div className="row">
    <div className="col-md-6">
<img className='img-fluid s-img' style={{height:"80%"}} src={servicesimg} alt="logo" />
    </div>
    <div className="col-md-6 s-child-2">
      <p>CHECK OUT OUR SERVICES</p>  
      <h3 className='text-danger'><b>Managed Ecommerce Services</b></h3>
      <p>TRVL offers managed e-commerce, technology, and design outsourcing services for product development to retailers and clients globally.<br/><br/>
You can pick and choose the services you need for a competitive retainer and commission fee model.</p>
   <div className="container">
       <div className="row">
               {servicepagedata.map(sdata)}
       </div>
   </div>
    </div>
</div>
</div>
{/* latest services work */}
<div className='container-fluid' style={{backgroundColor:"#09669f"}}>
<div className="row p-5">
<div className="col-md-6 text-white">
<p>OUR SERVICES</p>
<h4>Let's Check<br/> Our Services</h4>
<p>You can pick and choose the services you need for a competitive retainer and commission fee model.</p>

</div>
<div className="col-md-6">
<Accourdion/>
    </div>
</div>

</div>
<Footer/>



    </>
  )
}

export default ServicesPage