import React from 'react'

function Accourdion() {
  return (
    <div>
        <div className="accordion w-75" id="accordionExample">
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingOne">
      <button className="accordion-button text-danger" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
      <b>Online Marketing</b>
      </button>
    </h2>
    <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
      <div className="accordion-body">
        <strong>▶️SEO (on-page, off-page, ranking, backlinks)<br/>
▶️Conversion rate optimization with A/B testing<br/>
▶️Email newsletters<br/>
▶️Paid Marketing (Facebook and Google)<br/>
▶️Tailor-made graphics </strong> 
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingTwo">
      <button className="accordion-button collapsed text-danger" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
      <b>Technology Management</b>
      </button>
    </h2>
    <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
      <div className="accordion-body">
        <strong>▶️System administration services<br/>
        ▶️Uptime monitoring<br/>
        ▶️Hacking prevention<br/>
        ▶️Bug fixing<br/>
        ▶️Daily updates</strong> 
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingThree">
      <button className="accordion-button collapsed text-danger" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
      <b>Online Sales Operations</b>
      </button>
    </h2>
    <div id="collapseThree" className="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
      <div className="accordion-body">
        <strong>▶️Location-wise inventory management.<br/>
▶️Real-time logistics management.<br/>
▶️Real-time order tracking and order file generation.<br/>
▶️Customer service.<br/>
▶️SMS and email notifications of order status.</strong> 
      </div>
    </div>
  </div>
</div>
    </div>
  )
}

export default Accourdion