import React, { useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import '../styles/_ContactUs.scss'
import Footer from './Footer'
import Form from './Form'
import HeaderName from './HeaderName'
function ContactUs() {
  const location=useLocation();
  useEffect(() => {
    document.title = "Get Connect | TRVL";
  }, [])


  return (
    <div>
       <HeaderName name="Contact Us" location={location.pathname} />
       <figure>
           <img className='img-fluid m-5' src='../sources/images/contacts_01.jpg'  alt='contact us'/>
       </figure>
       
       <div className="container">
           <div className="row">
               <div className="col-md-6">
            <h6>GET IN TOUCH</h6>
            <h2><b>Contact Us</b></h2>
            <p>Whether you’re interested in working with or at Shopistan, please feel free to get in touch at the contact details below.</p>
               <div>
              <p><b>Phone:</b></p>
              <pre className='text-danger'>+92 306 444 7013</pre>
              <p><b>Send Email:</b></p>
              <pre className='text-danger'>mirzaarslan7013@gmail.com</pre>
              <p><b>Address:</b></p>
              <pre className='text-danger'>Nangal Sahdan Muridke</pre>
               </div>
                     </div>
               <div className="col-md-6 my-5">
                   <h2>Drop Us a Line</h2>
                   <p>Fill out our information form and we will get in touch with you as soon as possible</p>
               <Form/>
               </div>
           </div>
       </div>
       <Footer/>
    </div>
  )
}

export default ContactUs