import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/_Navbar.scss';

function Navbar() {
  return (
    <>
<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <div className="container-fluid">
    <Link className="navbar-brand" to="/"><img className='img-fluid w-50 toplogo' src='../sources/images/logo.png' alt="logo" /></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse " id="navbarSupportedContent">
      <ul className="navbar-nav d-flex justify-content-end m-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/services">Services</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/about-us">About Us</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/contact-us">Contact Us</Link>
        </li>
      </ul>
    </div>
  </div>
</nav>
    </>
  )
}

export default Navbar