import React from 'react'
import '../styles/_OurServices.scss';
import '../components/OurServicesdata';
import {servicesdata} from '../components/griddata';
function OurServices() {

  function sdata(s){
return(
  <div key={s.id} >
<img src={s.logoimg} className="img-fluid" alt="" srcset="" />
<h4>{s.heading}</h4>
<p>{s.para}</p>
  </div>
)
  }



  return (
    <div className='container mt-5'>
<div className="row jump">
    <div className="col-md">
    <h5>OUR SERVICES</h5>
    <h1>
    <span className='box text-danger'>
    <span>T</span>
  <span>R</span>
  <span>V</span>
  <span>L</span>
  <span>!</span> 
    </span>
    is a market leader in achieving the best Digital Marketing.</h1>

    </div>
    <div className="col-md"> 
    <div className="row row-cols-2 d-flex-control">
    {servicesdata.map(sdata)}

    </div>
</div>  
    </div>
</div>
   
  )
}

export default OurServices